import React from "react";

class NewAnnouncement extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      action: false,
    };
    this.changeAction = this.changeAction.bind(this);
    this.CreateAnnouncement = this.CreateAnnouncement.bind(this);
  }
  changeAction() {
    this.setState({ action: !this.state.action });
  }
  CreateAnnouncement() {
    let title = document.getElementById("title");
    let discription = document.getElementById("disc");
    let text = document.getElementById("text");
    if (title.value && discription.value && text.value) {
      let data = JSON.stringify({
        title: title.value,
        discription: discription.value,
        text: text.value,
      });
      let request = new XMLHttpRequest();
      request.open("POST", "https://185.237.206.119:9090/createData", true);
      request.setRequestHeader("Content-Type", "application/json");
      request.addEventListener("load", () => {
        if(!!request.response){
            window.location.reload();
        }else{
            alert("error")
        }
      });
      request.send(data);
    } else {
      alert("Заповніть всі поля");
    }
  }
  render() {
    if (this.state.action) {
      return (
        <div>
          <a
            className="btn-floating btn-large waves-effect waves-light red"
            onClick={this.changeAction}
          >
            <i className="material-icons">close</i>
          </a>

          <div className="row" id="create_">
            <div className="input-field col s6">
              <input id="title" type="text" />
              <label for="title">Title</label>
            </div>
            <div className="input-field col s6">
              <input id="disc" type="text" />
              <label for="disc">Discription</label>
            </div>
            <div className="input-field col s6">
              <input id="text" type="text" />
              <label for="text">Text</label>
            </div>
            <button
              className="btn waves-effect waves-light"
              style={{ marginTop: "3%" }}
              name="action"
              onClick={this.CreateAnnouncement}
            >
              Submit
              <i class="material-icons right">send</i>
            </button>
          </div>
        </div>
      );
    } else {
      return (
        <div>
          <a
            className="btn-floating btn-large waves-effect waves-light red"
            onClick={this.changeAction}
          >
            <i className="material-icons">add</i>
          </a>
        </div>
      );
    }
  }
}

export default NewAnnouncement;
