import React from "react";
import Card from "./Card";
import Data from "./data.json";

class TextPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      changes: true,
    };
    this.Edit = this.Edit.bind(this);
    this.ConfirmChanges = this.ConfirmChanges.bind(this);
    this.Delete = this.Delete.bind(this);
    this.Back = this.Back.bind(this)
  }
  Back(){
    window.location.reload();
  }

  Edit() {
    this.setState({ changes: !this.state.changes });
  }

  Delete() {
    let data = JSON.stringify({
      id: this.props.data.id,
    });
    let request = new XMLHttpRequest();
    request.open("POST", "https://185.237.206.119:9090/deleteData", true);
    request.setRequestHeader("Content-Type", "application/json");
    request.addEventListener("load", () => {
      if(!!request.response){
        window.location.reload();
    }else{
        alert("error")
    }
    });
    request.send(data);
  }

  ConfirmChanges() {
    let title = document.getElementById("title");
    let discription = document.getElementById("discription");
    let text = document.getElementById("text");
    let data = JSON.stringify({
      id: this.props.data.id,
      new_title: title.value,
      new_discription: discription.value,
      new_text: text.value,
    });
    let request = new XMLHttpRequest();
    request.open("POST", "https://185.237.206.119:9090/editData", true);
    request.setRequestHeader("Content-Type", "application/json");
    request.addEventListener("load", () => {
      if(!!request.response){
        window.location.reload();
    }else{
        alert("error")
    }
    });
    request.send(data);
  }

  render() {
    if (this.state.changes) {
      return (
        <div id="TextPage">
          <div id="bttnBlock">
            <button
              type="button"
              className="waves-effect waves-light btn-large"
              onClick={this.Back}
            >
              <span className="material-icons">arrow_back</span>
            </button>
            <button
              type="button"
              onClick={this.Edit}
              className="waves-effect waves-light btn-large"
            >
              <span className="material-icons">edit</span>
            </button>
            <button
              onClick={this.Delete}
              type="button"
              className="waves-effect waves-light btn-large"
            >
              <span className="material-icons">delete</span>
            </button>
          </div>
          <div id="main_text">
            <blockquote>
              <h1>{this.props.data.title}</h1>
            </blockquote>
            <p>{this.props.data.discription}</p>
            {this.props.data.text}
            <p style={{textAlign:'end'}}>{this.props.data.date}</p>
          </div>

          <div id="similar">
            {this.props.similar.map((elem) => {
              return (
                <Card
                  redir={false}
                  ChangeStatus={this.props.ChangeStatus}
                  data={elem}
                  key={elem.id}
                />
              );
            })}
          </div>
        </div>
      );
    } else {
      return (
        <div id="TextPage">
          <div id="bttnBlock">
            <button
              type="button"
              className="waves-effect waves-light btn-large"
              onClick={this.Back}
            >
              <span className="material-icons">arrow_back</span>
            </button>
            <button
              type="button"
              onClick={this.Edit}
              className="waves-effect waves-light btn-large"
            >
              <span className="material-icons">edit</span>
            </button>
            <button
              type="button"
              className="waves-effect waves-light btn-large"
              onClick={this.Delete}
            >
              <span className="material-icons">delete</span>
            </button>
          </div>

          <div id="main_text">
            <blockquote>
              <div className="row">
                Title:
                <input
                  type="text"
                  name="title"
                  id="title"
                  defaultValue={this.props.data.title}
                  className="validate"
                />
              </div>
            </blockquote>
            <p>
              <input
                id="discription"
                type="text"
                name="discription"
                defaultValue={this.props.data.discription}
              />
            </p>
            <div className="wrapper">
              <textarea
                id="text"
                cols="30"
                rows="15"
                defaultValue={this.props.data.text}
              ></textarea>
            </div>

            <button
              style={{ marginTop: "5px" }}
              className="btn waves-effect waves-light"
              type="button"
              onClick={this.ConfirmChanges}
            >
              Submit
              <i className="material-icons right">send</i>
            </button>
          </div>

          <div id="similar">
            {this.props.similar.map((elem) => {
              return (
                <div className="similar_card">
                  <Card
                    redir={false}
                    ChangeStatus={this.props.ChangeStatus}
                    data={elem}
                    key={elem.id}
                  />
                </div>
              );
            })}
          </div>
        </div>
      );
    }
  }
}

export default TextPage;
